import styles from './style.module.scss';
import { useState, useEffect } from 'react';
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';

const SearchForm = ({ placeholder = 'Search', className, debounceTime = 300 }) => {
  const [value, setValue] = useState('');
  const [results, setResults] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    if (value.length > 0) {
      const fetchResults = async () => {
        try {
          const response = await axios.get('https://api-goerli.reservoir.tools/search/collections/v2', {
            params: { name: value },
            headers: { accept: '*/*', 'x-api-key': 'demo-api-key' }
          });
          setResults(response.data.collections);
        } catch (error) {
          console.error(error);
        }
      };

      fetchResults();
    } else {
      setResults([]);
    }
  }, [value]);

  const handleChange = e => {
    setValue(e.target.value);
  };

  const handleReset = () => {
    setValue('');
    setResults([]);
  };

  const handleClick = (result) => {
    navigate(`/author/${result.collectionId}`);
    
  };

  return (
    <div className={`${styles.container} ${className || ''}`}>
      <form className={`${styles.form} search-form field bg-tertiary`}>
        <input
          className="text-overflow"
          type="text"
          placeholder={placeholder}
          value={value}
          onChange={handleChange}
        />
        {value.length !== 0 && (
          <button
            className={`${styles.clearBtn}`}
            type="button"
            aria-label="Clear"
            onClick={handleReset}
          >
            <i className="icon icon-xmark" />
          </button>
        )}
      </form>
      <ul className={styles.results}>
        {results.map(result => (
          <li key={result.collectionId}>
            <Link onClick={() => handleClick(result)}>
              {result.name}
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default SearchForm;
